	module.exports = {"data":{"markdownRemark":{"html":"<p>I started this new thing last week, <em>Friday Links</em>.  There’s a <a href=\"https://goo.gl/forms/MwEtFrDwFWzXSRv13\">feedback form</a> if you’re a regular reader and want to tell me what you think of it.  Otherwise, enjoy the links!</p>\n<h3>JavaScript And The Web</h3>\n<p> <a href=\"https://medium.com/javascript-inside/slaying-a-ui-antipattern-in-react-64a3b98242c\">Slaying a UI Antipattern in React – JavaScript Inside</a> - This is an interesting case study of how to work through an architectural problem using React.  I love seeing posts like this that take real world scenarios (I deal with this case of having data that might be in one of several loading states all the time), and identify re-usable patterns for solving them.</p>\n<p> <a href=\"https://jakearchibald.com/2017/netflix-and-react/\">Netflix functions without client-side React, and it’s a good thing - JakeArchibald.com</a> - A nice look at what Netflix’s experience removing client-side react from their landing page means and doesn’t mean.  I’m going to be really curious to see how the JS community gets better at enabling fast initial experiences while still having full rich interactivity when the users need it.</p>\n<h3>Programming Tools and Practices</h3>\n<p><a href=\"https://prettier.io/\">Prettier</a> - Prettier put out a new release this week and now supports Markdown as well as JavaScript, TypeScript, CSS, and GraphQL.  It has been my favorite new tool of 2017, solving a problem I didn’t know I had: the mental overhead of constantly reformatting my own code.  </p>\n<p><a href=\"http://amzn.to/2zvm12Z\">The Pragmatic Programmer - Andy Hunt &#x26; Dave Thomas</a> - Not a new book at all, but I like to recommend it at every opportunity.  It’s my favorite resource on the practice of Software Engineering and well worth checking out if you’ve never read it.</p>\n<h3>Non-Tech</h3>\n<p><a href=\"https://www.economist.com/news/briefing/21730870-economy-based-attention-easily-gamed-once-considered-boon-democracy-social-media\">Once considered a boon to democracy, social media have started to look like its nemesis - The Economist</a> - This is a wide-ranging take on the downsides of social media with a focus on it’s deleterious effects on our political systems.  If you’re on social media and/or you care about the future of politics in your place of living, it is worth a read.  </p>","frontmatter":{"title":"Weekly Links: November 10th 2017","keywords":"react social media comments economist","category":"opinion","date":"2017/11/10","path":"/2017/11/10/friday-links/","layout":"weekly-links","hideFooter":null},"fields":{"slug":"/2017/11/10/friday-links/"}}},"pathContext":{"slug":"/2017/11/10/friday-links/","relatedPosts":[{"path":"/2017/11/10/friday-links/","data":{"title":"Weekly Links: November 10th 2017","path":"/2017/11/10/friday-links/","description":"React, Social Media, and Comments","date":"2017/11/10"}},{"path":"/2017/07/19/ten-things-javascript/","data":{"title":"Ten Things A Serious JavaScript Developer Should Learn","path":"/2017/07/19/ten-things-javascript/","description":"Some of the important things to pick up in the JS world","date":"2017-07-19T04:00:00+00:00"}},{"path":"/2017/10/29/adequate-talks/","data":{"title":"How To Give A Perfectly Adequate Conference Talk","path":"/2017/10/29/adequate-talks/","description":"Tips for making your talk both useful and rewarding","date":"2017-10-30T02:00:00+00:00"}}]}}

